#about {
  width: 100%;
  min-height: 100vh;
  color: white;
  padding-bottom: 5vh;
  background: #222222cc;
  h3 {
    text-align: center;
    font-family: 'Source Code Pro', monospace;
    margin: 10px;
    text-decoration: underline;
    text-decoration-color: #00ffbb;
  }
  hr {
    margin: 20px;
  }
  .me-container {
    position: relative;
    background-color: rgba(0, 0, 0, 0.48);
    padding: 7px;
    width: 90%;
    margin: 10px auto;
  }
  .center {
    text-align: center;
    padding: 0 20px;
  }
 
  h2 {
    text-align: center;
    position: relative;
    transform: translateY(18vw);
    margin: 0;
  }
  p {
    position: relative;
  }
  #about-header {
    background: url(../../../images/about-header.jpg) no-repeat center center ; 
    // -webkit-background-size: cover;
    // -moz-background-size: cover;
    // -o-background-size: cover;
    background-size: cover;
    position: relative;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 27vw;
    border-bottom: 2px solid #00ffbb;

    #profile-pic {
      transform: translateY(50%);
      display: block;
      width: 50%;
      max-width: 300px;
      margin: 0 auto;
      
      img{
        width: 100%;
        border: 2px solid #00ffbb;
        border-radius: 50%;
        }
    }
  }
  @media screen and (min-width: 650px) {
    h2{
      transform: translateY(110px);
    }
    #about-header{
      margin-bottom: 160px;
    }   
  }
  .read-button2 {
    background: none;
    color: orange;
    cursor: pointer;
    outline: none;
    display: block;
    margin: 2vw auto;
    padding: 10px;
    border: 1px solid;
    transition: 0.4s;
    border-radius: 5px;
  }
  .read-button2:hover {
    color: rgb(40, 40, 40);
    background-color: orange;
    transform: scale(1.1);
    // font-weight: bold;
  }
  a,
  a:visited {
    color: orange;
  }
  a:hover {
    color: orangered;
  }
  .self {
    display: none;
    animation: slide-down 0.2s ease-in-out;
  }
  @keyframes slide-down {
    0% {
      width: 25%;
    }
    100% {
      width: 100%;
    }
  }
  ul{
    text-align: center;
    line-height: 20px;
    list-style: none;
  }
  #heroes {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    div {
      width: 33%;
      img {
        height: 100%;
        width: 100%;
      }
    }
  }

}

.side-note {
  color: orange;
  cursor: pointer;
}
